$sibRed: #e30613;
$sibElectricBlue: #3368ed;
$vpipeDarkBlue: #1e2232;
$sibSpringGreen: #78e0c2;
$mainRadius: 20px;
$mainRadiusTop: 30px 30px 0 0;
$mainRadiusBottom: 0 0 30px 30px;
$smallRadius: 10px;

@font-face {
  font-family: 'CabinetGrotesk-Variable';
  src: url('../fonts/CabinetGrotesk-Variable.woff2') format('woff2'),
       url('../fonts/CabinetGrotesk-Variable.woff') format('woff'),
       url('../fonts/CabinetGrotesk-Variable.ttf') format('truetype');
       font-weight: 100 900;
       font-display: swap;
       font-style: normal;
}

body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color:black;
  overflow-x: hidden;
  &:not(.homebody) {
    padding-top: 130px;
    .container {
      max-width: calc(100% - 150px);
      h2 {
        scroll-margin-top: 150px;
      }
    }
    #nav-container {
      top: 0;
      background: #d80c0c0f;
      -webkit-backdrop-filter: blur(4px);
      backdrop-filter: blur(4px);
      &.navScrolled {
        background: #d80c0c0f;
      }
      .container {
        padding: 10px 0;
      }
    }
    header {
      &:not(.home) {
        .container {
          max-width: calc(100% - 80px);
        }
      }
    }
  }
  font: {
    family: "Inter", sans-serif;
  }
  a {
    color: black;
  }
  h1, h2 {
    font: {
      family: "CabinetGrotesk-Variable", sans-serif;
    }
  }
  h1 {
    font: {
      size: 2.2em;
      weight: 400;
    }
    span {
      font: {
        weight: 700;
      }
    }
  }
  .container {
    width: 100%;
    max-width: calc(100% - 80px);
    margin: 0 auto;
    padding: 0 40px;
  }

  #nav-container {
    width: 100%;
    display: flex;
    justify-content: end;
    position: fixed;
    top: 40px;
    z-index: 9999;
    .container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .logos {
        width: inherit;
        a {
          img {
            width: 150px;
            padding-right: 12px;
          }
          &:last-child {
            img {
              width: 52px;
              padding-left: 20px;
              border-left: 1px solid #0000002b;
            }
          }
        }
      }
      button {
        width: 33%;
        display: none;
      }

      ul {
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        list-style: none;
    
        li {
          position: relative;
          margin: 0 20px;
          padding: 10px 0;
          line-height: 30px;
          margin-top: -2px;
          margin-bottom: -2px;
          span {
            position: absolute;
            top: 6px;
            right: -2px;
          }

          a {
            padding: 10px;
            color: black;
            text-decoration: none;
    
            &:hover {
              color: $sibElectricBlue;
            }
          }

          ul {
            min-width: 225px;
            padding: 20px 10px;
            display: none;
            position: absolute;
            top: 100%;
            left: 10px;
            z-index: 100;
            background: white;
            border-radius: $smallRadius;
            box-shadow: 0 0 30px -5px rgba(0,0,0,0.25);
            li {
              margin: 0 5px;
              padding: 0;
              line-height: 24px;
              a {
                display: block;
                padding: 10px 5px;
                color: black;
                text-decoration: none;
                border-radius: 5px;
                &:hover {
                  background: $sibSpringGreen;
                  color: black;
                }
              }
            }
          }
    
          &:hover {
            ul {
              display: block;
            }
          }

          &:last-child, &:nth-last-child(2) {
            ul {
              left: auto;
              right: 0;
            }
          }
        }
      }
    }
    &.navScrolled {
      background: rgba(255, 255, 255, 0.8);
      -webkit-backdrop-filter: blur(4px);
      backdrop-filter: blur(4px);
    }
  }
  
  header {
    height: auto;
    padding: 180px 20px 40px 20px;
    position: relative;
    background: rgb(255,244,244);
    background: linear-gradient(328deg, rgba(231,238,245,.9) 0%, rgba(255,237,237,.9) 100%);
    -webkit-backdrop-filter: blur(4px);
    backdrop-filter: blur(4px);
    border-radius: $mainRadiusBottom;
    .container {
      .hero-carousel {
        width: 100%;
        margin: 60px 0;
        position: relative;
        overflow: hidden;
        .slide {
          display: none;
          color: black;
          &.active {
            display: flex;
          }
          .image {
            flex: 1;
            img {
              width: 100%;
              height: auto;
              border-radius: 10px;
            }
          }
          .video {
            flex: 1;
            position: relative;
            width: 100%;
            padding-bottom: 29.2%;
            overflow: hidden;
            iframe {
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              border-radius: $smallRadius;
            }
          }
          .content {
            width: 50%;
            padding: 20px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: start;
            flex: 1;
            h1 {
              font: { 
                size: 2em;
                weight: 700;
              }
              margin: 5px 0;
            }
            p {
              font-size: 1.2em;
              margin: 5px 0 20px;
            }
            .cta {
              width: auto;
              display: inline-flex;
              flex-grow: 0;
              padding: 15px 30px;
              background: $sibRed;
              color: #fff;
              text-decoration: none;
              border-radius: 5px;
              transition: background 0.3s ease;
              &:hover {
                background: $vpipeDarkBlue;
              }
            }
          }
        }
      }
    
      .slide-indicators {
        display: flex;
        padding: 8px;
        position: absolute;
        bottom: 30px;
        left: 50%;
        transform: translateX(-50%);
        background: white;
        border-radius: 10px;
        .indicator {
          display: inline-block;
          width: 30px;
          height: 8px;
          background: #ccc;
          border-radius: 10px;
          margin: 0 5px;
          cursor: pointer;
          &.active {
            background: $sibRed;
          }
        }
      }
   }
   &:not(.home) {
    min-height: auto;
    margin: 40px;
    padding: 0;
    background: #dffff8;
    border-radius: $mainRadius;
   }
  }

  #get-started {
    margin: 160px 0;
    position: relative;
    .metaball {
      position: absolute;
      right: 50px;
      top: -275px;
      z-index: -1;
      opacity: 0;
      animation: fadeInMoveDown 2s ease-in-out forwards;
  
      img {
        width: 250px;
      }
    
      &:first-child {
        left: 10px;
        right: auto;
        top: -290px;
        animation: fadeInMoveUp 1s ease-in-out forwards;
        animation-delay: 1s;

        img {
          width: 200px;
        }

      }
    }
    
    @keyframes fadeInMoveUp {
      from {
        opacity: 0;
        transform: translateY(-20px) rotate(90deg);
      }
      to {
        opacity: 1;
        transform: translateY(0) rotate(90deg);
      }
    }
    
    @keyframes fadeInMoveDown {
      from {
        opacity: 0;
        transform: translateY(20px);
      }
      to {
        opacity: 1;
        transform: translateY(0);
      }
    }    
    
    h1 {
      padding: 20px 0;
      text-align: center;
    }
    .container {
      justify-content: space-between;
      .homepage-columns {
        display: flex;
        justify-content: space-evenly;
        gap: 20px;
        .column {
          width: 25%;
          padding: 20px 20px 80px 20px;
          position: relative;
          // background: #c0fbea;
          background: #fff4f4;
          border-radius: $smallRadius;
          .column-heading {
            padding: 20px;
            text-align: center;
            background: rgba(255, 255, 255, 0.8);
            border-radius: $smallRadius;
            img {
              height: 55px;
            }
            h2 {
              margin: 0;
              font: {
                weight: 400;
              }
            }
          }
          h4 {
            display: inline-block;
            margin: 20px 0 0;
            padding: 2px 5px;
            font: {
              size: 1em;
              weight: 300;
            }
            background: $sibRed;
            color: white;
            border-radius: 10px;
          }
          ul {
            padding: 0 0 0 20px;
            li {
              padding: 8px 0;
              list-style-image: url(../img/ic-list-style.svg);
              a {
                color: black;
                text-decoration: none;
                &:hover {
                  color: $sibElectricBlue;
                }
              }
            }
          }
          &:after {
            content: '';
            width: 100px;
            height: 8px;
            position: absolute;
            bottom: 25px;
            left: 0;
            right: 0;
            margin-left: auto;
            margin-right: auto;
            // background: rgba(0, 0, 0, 0.148);
            background: #ff00001a;
            border-radius: $smallRadius;
          }
        }
      }
    }
  }

  #vpipe-numbers {
    margin: 220px 0 70px;
    position: relative;
    text-align: center;
    .dna-txt {
      width: 48%;
      overflow: hidden;
      position: absolute;
      top: -40px;
      left: 0;
      // color: $sibRed;
      background: linear-gradient(to right, rgba(226,6,19,.5) 0%, rgba(226,6,19,0) 100%);
      -webkit-background-clip: text;
      background-clip: text;
      color: transparent;
      font: {
        family: "Courier";
        size: 1.2em;
      }
      &:nth-child(2) {
        left: auto;
        top: 0px;
        right: 0;
        background: linear-gradient(to left, rgba(51,104,237,.5) 0%, rgba(51,104,237,0) 100%);
        -webkit-background-clip: text;
        background-clip: text;
        color: transparent;
      }
    }
    .bg-full-width {
      background: rgb(255,244,244);
      background: linear-gradient(328deg, rgba(255, 244, 244, 0.7) 0%, rgba(231, 238, 245, 0.7) 100%);
      -webkit-backdrop-filter: blur(4px);
      backdrop-filter: blur(4px);
      border-radius: $mainRadius;
      .container {
        display: flex;
        gap: 20px;
        margin: 80px auto;
        padding: 180px 0;
        .column-numbers {
          width: 33%;
          padding: 40px 0;
          display: flex;
          justify-content: center;
          align-items: center;
          background: rgba(255,255,255,0.4);
          border-radius: $smallRadius;
        }
      }
    }
  }

  .half-circle {
    position: absolute;
    left: -20px;
    z-index: -1;
    margin-top: -200px;
    img {
      width: 320px;
    }
  }

  #why-vpipe {
    padding: 160px 0;
    text-align: center;
    .container {
      display: flex;
      gap: 20px;
      margin: 20px auto;
      .column {
        width: 25%;
        padding: 60px 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        -webkit-backdrop-filter: blur(4px);
        backdrop-filter: blur(4px);
        border: 1px solid #eee;
        border-radius: $smallRadius;
        .img-container {
          height: 80px;
          display: flex;
          align-items: end;
          justify-items: center;
        }
        h2 {
          font: {
            weight: 300;
          }
          margin-bottom: 0;
        }
      }
    }
  }

  #meet-the-team {
    padding: 160px 0;
    position: relative;
    text-align: center;
    overflow: hidden;
    .container {
      display: flex;
      justify-content: space-evenly;
      gap: 20px;
      .column {
        width: 50%;
        padding: 40px;
        background: $sibElectricBlue;
        color: white;
        border-radius: $smallRadius;
        h2 {
          margin-top: 0;
          text-transform: uppercase;
          font: {
            weight: 400;
          }
          span {
            font: {
              weight: 700;
            }
          }
        }
        ul {
          padding: 0;
          text-align: start;
          li {
            display: flex;
            justify-content: space-between;
            padding: 5px 0;
            span {
              &:first-child {
                padding: 0 20px 0 0;
              }
            }
          }
        }
        &:last-child {
          background: #78e0c2d5;
          color: $vpipeDarkBlue;
          -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
        }
      }
    }
    .blue-abstract-circle {
      position: absolute;
      top: 0;
      right: -250px;
      z-index: -1;
      img {
        width: 800px;
        height: 800px;
      }
    }
  }

  #funding {
    margin: 160px 0;
    h1 {
      text-align: center;
    }
    .container {
      display: flex;
      justify-content: space-evenly;
      gap: 20px;
      .column {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 40px 0;
        border: 1px solid #ddd;
        border-radius: $smallRadius;
        a {
          img {
            width: 200px;
          }
        }
      }
    }
  }

  #footer {
    margin: 160px 0 0;
    padding: 0 0 20px;
    background: $sibSpringGreen;
    border-radius: $mainRadiusTop;
    .container {
      padding: 80px 0 0;
      .row {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        .column {
          padding: 20px;
          h1 {
            font: {
              size: 1.4em;
              weight: 700;
            }
          }
          ul {
            padding: 0 0 0 17px;
            li {
              margin: 8px 0;
              padding: 5px 0;
              list-style-image: url(../img/ic-list-style.svg);
              a {
                color: black;
                text-decoration: none;
                &:hover {
                  color: $sibElectricBlue;
                }
              }
            }
          }
        }
        &:nth-child(2) {
          margin: 80px 0;
          .column {
            width: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            border: 1px solid rgba(0, 0, 0, 0.2);
            border-radius: $smallRadius;
            h1 {
              margin-top: 0;
            }
            .social-container {
              display: flex;
              a {
                width: 40px;
                height: 40px;
                margin: 0 10px;
                display: flex;
                justify-content: center;
                align-items: center;
                padding: 10px;
                background: rgba(255, 255, 255, 0.3);
                border-radius: $smallRadius;
                transition: background .3s ease;
                &:hover {
                  background: white;
                }
              }
            }
          }
        }
        &:last-child {
          justify-content: center;
        }
      }
    }
  }

}

@media screen and (min-width: 1600px) {
  body {
    .container {
      width: 100%;
      max-width: 1500px;
      margin: 0 auto;
      padding: 0 40px;
    }
    header {
      min-height: 590px;
    }
  }
}

@media screen and (max-width: 1350px) {
  body {
    #meet-the-team {
      .container {
        display: grid;
        .column {
          width: auto;
          margin: 10px 0;
        }
      }
    }
  }
}

@media screen and (max-width: 1218px) {
  body {
    #get-started {
      .container {
        .homepage-columns {
          .column {
            .column-heading {
              h2 {
                font: {
                  size: 1em;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1024px) {
  body {
    .container {
      max-width: calc(100% - 10px);
      padding: 0;
    }
    #nav-container {
      .container {
        max-width: calc(100% - 50px);
        .logos {
          a {
            img {
              width: 120px;
              padding-right: 5px;
            }
            &:last-child {
              img {
                width: 40px;
                padding-left: 10px;
              }
            }
          }
        }
        nav {
          display: none;
          &.show {
            display: block;
            width: auto;
            position: absolute;
            top: 80px;
            right: 20px;
            z-index: 9999;
            background: white;
            box-shadow: 0 0 20px -10px rgba(0, 0, 0, 0.6);
            border-radius: $smallRadius;
            &:before {
              content: "";
              position: absolute;
              top: -10px;
              right: -10px;
              transform: translateX(-50%);
              border-left: 20px solid transparent;
              border-right: 20px solid transparent;
              border-bottom: 20px solid white;
            }
          
            ul {
              display: block;
              li {
                span {
                  display: none;
                }
                ul {
                  padding: 10px;
                  display: block;
                  position: unset;
                  box-shadow: none;
                  li {
                    a {
                      padding: 5px;
                    }
                  }
                }
              }
            }
          }          
        }
        button {
          display: flex;
          align-items: end;
          flex-direction: column;
          background: none;
          border: none;
          cursor: pointer;
          span {
            width: 35px;
            height: 2px;
            display: block;
            background: $sibRed;
            margin: 4px 0;
            border-radius: $smallRadius;
            &:nth-child(2) {
              position: relative;
              left: 5px;
            }
          }
        }
      }
    }
    header {
      padding-top: 130px;
      .container {
        .hero-carousel {
          .slide.active {
            display: block;
            .content {
              width: 100%;
              padding-left: 0;
            }
            .video {
              padding-bottom: 56%;
            }
          }
        }
      }
    }
    #get-started {
      margin-top: 80px;
      .container {
        .homepage-columns {
          display: grid;
          grid-template-columns: 50% 50%;
          grid-row: auto auto;
          grid-column-gap: 20px;
          grid-row-gap: 20px;
          justify-content: space-around;
          .column {
            width: auto;
            .column-heading {
              h2 {
                font: {
                  size: 1.2em;
                }
              }
              img {
                height: 45px;
              }
            }
          }
        }
      }
    }
    #vpipe-numbers {
      .bg-full-width {
        .container {
          .column-numbers {
            h1 {
              font: {
                size: 1.4em;
              }
            }
          }
        }
      }
    }
    .half-circle {
      opacity: .2;
    }
    #why-vpipe {
      .container {
        display: grid;
        grid-template-columns: 50% 50%;
        grid-column-gap: 20px;
        grid-row-gap: 20px;
        justify-content: space-around;
        .column {
          width: auto;
          h2 {
            font: {
              size: 1.2em;
            }
          }
        }
      }
    }
    #meet-the-team {
      .container {
        display: block;
        .column {
          width: auto;
          margin: 20px 0;
        }
      }
      .blue-abstract-circle {
        opacity: .2;
      }
    }
    #funding {
      margin: 80px 0;
      padding: 40px 0 0;
      .container {
        .column {
          a {
            img {
              width: 120px;
            }
          }
        }
      }
    }
    #footer {
      margin: 100px 0 0;
      .container {
        .row, .row:nth-child(2) {
          display: grid;
          grid-template-columns: 50% 50%;
          grid-column-gap: 20px;
          grid-row-gap: 20px;
          justify-content: space-around;
          .column {
            width: auto;
            .social-container {
              a {
                width: 20px;
                height: 20px;
                img {
                  width: 18px;
                }
              }
            }
          }
        }
        .row:last-child {
          display: flex;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  body {
    &:not(.homebody) {
      .container {
        max-width: calc(100% - 60px);
      }
      #nav-container {
        .container {
          padding: 20px 0;
        }
      }
      header {
        &:not(.home) {
          margin: 20px 10px 10px 10px;
          .container {
            max-width: calc(100% - 40px);
            padding: 10px 0;
          }
        }
      }
    }
    header {
      .container {
        .hero-carousel {
          margin: 0 0 40px;
          .slide {
            .content {
              h1 {
                font: {
                  size: 1.8em;
                }
              }
            }
          }
        }
        nav {
          &.show {
            top: 95px;
          }
        }
      }
    }
    #get-started {
      margin: 50px 15px 0;
      position: relative;
      z-index: -1;
      .metaball {
        display: none;
        position: absolute;
        z-index: -1;
        img {
          width: 150px;
        }
        &:first-child {
          display: block;
          left: -40px;
          top: -200px;
          img {
            width: 200px;
          }
        }
      }
      .container {
        .homepage-columns {
          display: grid;
          grid-template-columns: 100%;
          grid-row: auto;
          .column {
            width: auto;
            .column-heading {
              h2 {
                font: {
                  size: 1.2em;
                }
              }
              img {
                height: 45px;
              }
            }
          }
        }
      }
    }
    #vpipe-numbers {
      margin: 80px 0 70px;
      .bg-full-width {
        .container {
          display: block;
          padding: 50px 0;
          .column-numbers {
            width: 90%;
            margin: 20px auto;
          }
        }
      }
    }
    #why-vpipe {
      padding: 0;
      .container {
        display: block;
        .column {
          margin: 20px 0;
        }
      }
    }
    #meet-the-team {
      padding: 160px 0 0;
      .container {
        .column {
          ul {
            li {
              font: {
                size: .85em;
              }
            }
          }
        }
      }
    }
    #funding {
      padding: 0;
    }
    #footer {
      .container {
        padding: 30px 0 0;
        .row {
          display: block;
          .column {
            margin: 0;
            padding: 1px 20px;
          }
          &:nth-child(2) {
            display: block;
            margin: 20px;
            .column {
              margin: 20px 0;
              padding: 20px;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 375px) {
  body {
    #nav-container {
      .container {
        nav {
          &.show {
            ul {
              li {
                ul {
                  display: none;
                }
              }
            }
          }
        }
      }
    }
  }
}